.filter {
  width: 100%;
  background-color: white;
  border-radius: 4px;
  overflow: hidden;
  height: 0px;
  transition: height 0.25s ease-in-out;
  margin-top: 8px;
}
.filter.open {
  height: 250px;
}
.filter .container {
  padding: 24px;
  width: 100%;
  height: 100%;
  display: flex;
  position: relative;
}
.filter .container .filterContent {
  display: flex;
  flex-direction: column;
  margin-right: 64px;
}
.filter .container .multiColumns {
  display: flex;
}
.filter .container .filterContent .title {
  font-weight: 700;
  margin-bottom: 16px;
}
.filter .container .btn {
  position: absolute;
  left: 16px;
  bottom: 16px;
  width: 100px
}
